body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* HOME PAGE */
.Header,.HomePageGrid,.profile-card{
  background: #c0392b;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #8e44ad, #c0392b);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #8e44ad, #c0392b); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.HomePageGrid{
  position: fixed;
  left: 0;
  top: 60px;
  height: 100%;
  width: 100%;
  text-align: center;
}
@media screen and (max-width: 1024px){
  .HomePageGrid{top: 0;padding-top: 5em !important;}
}
.Header .mdl-layout-title,
.Header .mdl-layout__drawer .mdl-layout__title{
  font-size: 25px;
  text-transform: lowercase;
  font-family: 'Coiny', cursive;
  letter-spacing: .2rem;
}
.Header .mdl-navigation__link {
    text-transform: uppercase !important;
    font-family: 'Coiny', cursive !important;
    transition: color .4s ease-out;
}
.Header .mdl-navigation__link:hover {color: rgba(158,158,158, 0.90);}
.profile-descrip{margin-top: 6em;}

/* font-family: 'Coiny', cursive; */
.profile-text h3 {
    text-transform: uppercase;
    letter-spacing: .1em;
    margin-top: 30px;
    margin-bottom: 13px;
    font-family: 'Coiny', cursive;
    font-size: 25px;
}
.profile-text h3 span {
  color: #fff;
}
.profile-text h5 {
    margin-top: 0;
    margin-bottom: 15px;
    font-weight: 600;
    font-size: 20px;
}
.profile-text h5 span {color: #fff}

.profile-text h3, .profile-text h5 {
    text-align: center;
    line-height: 1;
}

.img-wrap {
    width: 250px;
    margin: auto;
    position: relative;
    top: 30px;
    height: 250px;
    text-align: center;
    z-index: 10;
    overflow: hidden;
    border-radius: 50px;
    box-shadow: 0px 4px 18px rgba(0,0,0,0.1);
    background: rgba(255, 255, 255, 0.31);
    opacity: 1;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    transition: all .4s ease;
}
.img-wrap:hover{  
  box-shadow: 0px 1px 14px rgba(51, 51, 51, 0.68);
  -webkit-box-shadow: 0px 1px 14px rgba(51, 51, 51, 0.68);
  border-radius: 50%;
} 
.img-wrap img {
  width: 100%;
  height: 300px;
  -ms-transform: translate(0%, -5%) rotate(-45deg);
  transform: translate(0%, -5%) rotate(-45deg);
}
@media screen and (max-width: 1024px){
  .mdl-layout__drawer-button {
    margin: 15px !important;
    background: #c0392b;
    border-radius: 5px;
    transition: all .4s ease-out;
    color: #fff !important;
  }
  .mdl-layout__drawer-button:hover{
    background: #c0392b;
    background: linear-gradient(to left, #8e44ad, #c0392b);
    color: rgba(158,158,158, 0.90) !important;
  }
}
/* END HOME PAGE */

/* START ABOUT PAGE */
.profile-card{
  text-align: center;
  padding: 10px;
  border-radius: 20px;  
  box-shadow: 5px 2px 20px rgba(51, 51, 51, 0.6);
}
.profile-card:after{
  content: '';
  position: absolute;
}
.avatar_img{
  width: 200px;
  height: 200px;
  border-radius: 50%;
  box-shadow: 0px 5px 7px rgba(51, 51, 51, 0.68);
  -webkit-box-shadow: 0px 5px 7px rgba(51, 51, 51, 0.68);
