.about-description {
    max-height: 360px;
    overflow: hidden;
}
.about-description h5{font-weight: 600;}
.about-page h5 span {color: #974297;}
.about-description p {
    line-height: 28px;
    font-size: 18px;
    letter-spacing: .1rem;
    margin-top: 15px;
    padding-left: 20px;
    padding-right: 20px;
