.work-experience{
  padding-top: 2em;
  padding-bottom: 4em;
}
.expirience-list {
    margin-bottom: 0;
    padding-left: 0;
}

.expirience-list li {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    padding-left: 0;
    position: relative;
}

.expirience-list li:nth-child(odd) {
    -ms-flex-pack: end;
    justify-content: flex-end
}

.expirience-list li:nth-child(odd) .expirience-block::after {
    left: 0;
    right: auto;
    top: 50%;
    border: 15px solid transparent;
    border-right: 15px solid #fafafa;
    -ms-transform: translate(-100%, -50%);
    transform: translate(-100%, -50%)
}

.expirience-list li:last-child::before {
    display: none
}

.expirience-list li::after {
    display: none
}

.expirience-list li::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 1px;
    height: 100%;
    background-color: #c0392b;
}

.expirience-list li .dot {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 5;
    width: 14px;
    height: 14px;
    border: 1px solid #9747a4;
    border-radius: 3px;
    background: #fff;
    -ms-transform: translate(-50%, -50%) rotate(45deg);
    transform: translate(-50%, -50%) rotate(45deg)
}

.expirience-list li .dot::before {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 10;
    width: 7px;
    height: 7px;
    background: #c0392b;
    border-radius: 1px;
    -ms-transform: translate(-50%, -50%) rotate(0deg);
    transform: translate(-50%, -50%) rotate(0deg)
}

.expirience-list .expirience-block {
    position: relative;
    width: 100%;
    max-width: 495px;
    padding: 25px 30px;
    background: #fafafa;
    box-shadow: 0px 4px 18px rgba(0, 0, 0, 0.1);
    border-radius: 20px
}

.expirience-list .expirience-block::after {
    content: '';
    position: absolute;
    right: 0;
    top: 50%;
    z-index: 10;
    border: 15px solid transparent;
    border-left: 15px solid #fafafa;
    -ms-transform: translate(100%, -50%);
    transform: translate(100%, -50%)
}

.expirience-list .expirience-block h4 {
    margin-bottom: 5px;
    font-weight: 600
}

