/* START testimonial */
.testimonials {
    padding-top: 40px;
    padding-bottom: 40px;
    background-color: #fafafa;
}

.testimonial-block {
    display: -ms-flexbox !important;
    display: -webkit-flex !important;
    display: flex !important;
    -ms-flex-align: center;
