}

.testimonial-block .testimonial-logo img {
    position: absolute;
    left: 50%;
    top: 0;
    max-width: none;
    -ms-transform: rotate(-45deg) translate(-22%, -40%);
    transform: rotate(-45deg) translate(-22%, -40%)
}

.testimonial-block .testimonial-desc {
    padding-left: 95px;
    border-radius: 30px;
    box-shadow: 0px 4px 18px rgba(0, 0, 0, 0.16);
