    font-weight: 600;
}

.testimonial-block .testimonial-desc .possition {
    position: relative;
    margin-bottom: 0;
    font-style: italic;
    color: #974297;
    z-index: 3
}

.testimonial-block .testimonial-desc .possition::after {
    content: '';
    position: absolute;
    bottom: 0;
    right: 10px;
    width: 30px;
    height: 30px;
    border: 1px solid #b43b49;
    border-radius: 5px;
    margin: 0 3px;
    -webkit-transition: all .3s;
    transition: all .3s;
    -webkit-transform: rotate(60deg);
    transform: rotate(60deg);
}
.testimonial-block:hover .testimonial-desc .possition::after {
  border: 1px solid #974297;
  transform: rotate(60deg) scale(1.5);
  -webkit-transform: rotate(60deg) scale(1.5);
}

