.section-title {
    max-width: 650px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}
.section-title .page-title{
  text-transform: capitalize;
  letter-spacing: .1em;
  font-size: 35px;
  font-weight: 400;
  margin: 7px 0;
  font-family: 'Coiny', cursive;

}
.section-title .page-title span {color: #974297;}
.section-title .title-dvider {
    position: relative;
    display: -ms-inline-flexbox;
