  top: 18%; 
}
.animate-ico2 img {
    width: 32px;
    height: 32px;
    animation: movebounce 2.1s linear infinite; 
  }
.animate-ico3{
  left: 26%;
  top: 10%;
}
.animate-ico3 img{animation: moveleftbounce 2.1s linear infinite; }
.animate-ico-rocket{
  left: -50px;
  bottom: 10%;
  animation: rocket 2.1s linear 2s; 
}

.animate-ico,
.animate-ico2,
.animate-ico3,
.animate-ico-rocket{position: absolute;}
.animate-ico img, 
.animate-ico2 img,
.animate-ico3 img,
.animate-ico-rocket img {width: 32px;height: 32px;}
  @keyframes rocket {
  0% {
    transform: translateX(0px); }
  50% {
    transform: translateX(50%); }
  100% {
    transform: translateX(0px);
    left: 100%;
    bottom: 100%
   } }

  @keyframes moveleftbounce {
  0% {
    transform: translateX(0px); }
  50% {
    transform: translateX(8px); }
  100% {
    transform: translateX(0px); } }

  @keyframes movebounce {
  0% {
    transform: translateY(0px); }
  50% {
    transform: translateY(8px); }
  100% {
    transform: translateY(0px); } }


.profile-2::before {
    animation: 2.5s anim-effct-slide 0s linear infinite;
    box-shadow: 0 0 0 80px hsla(0,0%,91%,.1);
    position: absolute;
    content: "";
    width: 24px;
    height: 24px;
    top: 28%;
    left: 28%;
    border-radius: 50%;
    z-index: 0;
}
@keyframes anim-effct-slide {
  0% {
    box-shadow: 0 0 0 8px #e0e0e0;
    opacity: .2; }
  50% {
    box-shadow: 0 0 0 15px rgba(224, 224, 224, 0.6);
    opacity: .5; }
  100% {
    box-shadow: 0 0 0 25px rgba(224, 224, 224, 0);
    opacity: .5; } }
/* animation-circle */
.animation-circle.absolute i {
  left: -70px;
  top: 28%; }

.animation-circle{
  position: absolute;
  bottom: 20%;
  left: 0;
  }

.animation-circle i {
  position: absolute;
  height: 100px;
  width: 100px;
  background: linear-gradient(to right, #b53b47 0%, #a0407c 100%);
  background: -webkit-linear-gradient(to right, #b53b47 0%, #a0407c 100%);
  border-radius: 100%;
  opacity: 0.5;
  transform: scale(1.3);
  -webkit-animation: ripple1 3s linear infinite;
  animation: ripple1 3s linear infinite; }
  .animation-circle i:nth-child(2) {
    -webkit-animation: ripple2 3s linear infinite;
    animation: ripple2 3s linear infinite; }
  .animation-circle i:nth-child(3) {
    -webkit-animation: ripple3 3s linear infinite;
    animation: ripple3 3s linear infinite; }

@keyframes ripple1 {
  0% {
    transform: scale(5.5);
    opacity: 0.3; }
  100% {
    transform: scale(8.5);
    opacity: 0.0; } }

@-webkit-keyframes ripple1 {
  0% {
    -ms-transform: scale(5.5);
    /* IE 9 */
    -webkit-transform: scale(5.5);
    /* Safari */
    transform: scale(5.5);
    opacity: 0.3; }
  100% {
    -ms-transform: scale(8.5);
    /* IE 9 */
    -webkit-transform: scale(8.5);
    /* Safari */
    transform: scale(8.5);
    opacity: 0.0; } }

@keyframes ripple2 {
  0% {
    -ms-transform: scale(3.5);
    /* IE 9 */
    -webkit-transform: scale(3.5);
    /* Safari */
    transform: scale(3.5); }
  100% {
    -ms-transform: scale(5.5);
    /* IE 9 */
    -webkit-transform: scale(5.5);
    /* Safari */
    transform: scale(5.5); } }

@-webkit-keyframes ripple2 {
  0% {
    -ms-transform: scale(3.5);
    /* IE 9 */
    -webkit-transform: scale(3.5);
    /* Safari */
    transform: scale(3.5); }
  100% {
    -ms-transform: scale(5.5);
    /* IE 9 */
    -webkit-transform: scale(5.5);
    /* Safari */
    transform: scale(5.5); } }

@keyframes ripple3 {
  0% {
    -ms-transform: scale(1.5);
    /* IE 9 */
    -webkit-transform: scale(1.5);
    /* Safari */
    transform: scale(1.5); }
  100% {
    -ms-transform: scale(3.5);
    /* IE 9 */
    -webkit-transform: scale(3.5);
    /* Safari */
    transform: scale(3.5); } }

@-webkit-keyframes ripple3 {
  0% {
    -ms-transform: scale(1.5);
    /* IE 9 */
    -webkit-transform: scale(1.5);
    /* Safari */
    transform: scale(1.5); }
  100% {
    -ms-transform: scale(3.5);
    /* IE 9 */
    -webkit-transform: scale(3.5);
    /* Safari */
    transform: scale(3.5); } }

